.favorite-container {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.favorite-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
}

.favorite-icon svg {
  transition: opacity 0.2s ease;
}

.favorite-container:hover .favorite-icon svg {
  opacity: 0.8;
}

.favorite-tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 8px;
  padding: 8px 12px;
  background-color: #000000;
  color: white;
  font-size: 12px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  border-radius: 6px;
  white-space: nowrap;
  z-index: 1000;
  pointer-events: none;
}

.favorite-tooltip::after {
  content: '';
  position: absolute;
  top: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #000000;
}

/* Remove any default margins from Streamlit */
[data-testid="stVerticalBlock"] > div:has(> [data-testid="element-container"] > .stFavorite) {
  margin-left: 0 !important;
  margin-right: 0 !important;
}